* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

.wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    /*grid-auto-rows: 300px;*/
    grid-template-areas:
    "a a"
    "a a";
    /*border: black 1px solid;*/
    width: 100vw;
    height: 100vh;
    /*margin: 3vh 3vw;*/
}

.sub-wrapper {
    width: 400px !important;
    height: 100vh !important;
    /*border: aquamarine 2px dashed;*/
    grid-area: a;
    align-self: center;
    justify-self: center;
}

.phone-wrapper {
    width: 100% !important;
    height: 100% !important;
    /*border: red 2px dashed;*/
    align-self: center;
    justify-self: center;
    padding-top: 70px;
    padding-left: 25px;
    padding-right: 25px;
}

.image-back {
    z-index: -100;
    position: fixed;
    right: 0;
    left: 0;
    margin-right: auto;
    margin-left: auto;
    min-height: 10em;
    width: 400px;
}
